'use client'; 
import { useEffect, useState } from 'react';

export default function Slider({ Slides: initialSlides, id }: any) {
  // Estado para manejar los slides con sus imágenes
  const [currentSlides, setCurrentSlides] = useState(initialSlides || []);
  const baseUrl = process.env.NEXT_PUBLIC_STRAPI_URL || "http://localhost:1337";

useEffect(() => {
    async function fetchSliderImages() {
      try {
        // Usamos el nombre exacto: sections.slider-personalizable
        const res = await fetch(`${baseUrl}/api/home?populate[secciones][on][sections.slider-personalizable][populate]=Slides.imagen`);
        const result = await res.json();

        console.log("Respuesta Strapi Slider:", result);

        // Buscamos usando el nombre largo y correcto
        const secciones = result?.data?.secciones || result?.data?.attributes?.secciones;
        
        if (secciones) {
          const sliderData = secciones.find(
            (s: any) => s.__component === "sections.slider-personalizable"
          );

          if (sliderData?.Slides) {
            setCurrentSlides(sliderData.Slides);
          }
        }
      } catch (error) {
        console.error("Error cargando imágenes del slider:", error);
      }
    }

    fetchSliderImages();
  }, [baseUrl]);

  return (
    <section className="py-16 bg-white">
      <div className="container mx-auto px-4 flex overflow-x-auto gap-8 pb-10 scrollbar-hide">
        {currentSlides?.map((slide: any, index: number) => {
          
          // Buscamos la URL de la imagen (probando diferentes rutas de Strapi)
          const imgPath = slide.imagen?.url || slide.imagen?.data?.attributes?.url;
          const imgUrl = imgPath ? `${baseUrl}${imgPath}` : null;

          // Limpieza de textos (ajustado a tu estructura de Rich Text)
          const textoDescripcion = slide.descripcion?.[0]?.children?.[0]?.text || "";
          
          // Extraemos la URL y label del botón del slide
          // Nota: Verifica si en Strapi el campo es 'boton' (un solo objeto) o array
          const botonData = Array.isArray(slide.boton) ? slide.boton[0] : slide.boton;
          const textoBoton = botonData?.label || "Leer más";
          const enlaceBoton = botonData?.URL || "#"; 

          return (
            <div key={index} className="min-w-[350px] bg-gray-50 rounded-3xl shadow-md border border-gray-100 overflow-hidden">
              <div className="h-64 bg-gray-200">
                {imgUrl ? (
                  <img 
                    src={imgUrl} 
                    className="w-full h-full object-cover" 
                    alt={slide.titulo} 
                  />
                ) : (
                  <div className="flex items-center justify-center h-full text-gray-400 font-medium italic">
                    Cargando imagen...
                  </div>
                )}
              </div>
              <div className="p-8">
                <h3 className="text-2xl font-bold mb-3">{slide.titulo}</h3>
                <p className="text-gray-600 mb-6 line-clamp-2">{textoDescripcion}</p>
                <a 
                  href={enlaceBoton} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="text-blue-600 font-bold hover:text-blue-800 transition-colors"
                >
                  {textoBoton} →
                </a>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
}